import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    debug: true,
    fallbackLng: 'en',
    lng: 'ar', // Set Arabic as default language
    interpolation: {
      escapeValue: false, // not needed for react as it escapes by default
    },
    resources: {
      ar: {
        translation: {
          "dashboard_title": "  ",
          "previous_day": " ",
          "next_day": " ",
          "date": "",
          "enter_daily_metrics": "  ",
          "website_visits": " ",
          "app_downloads": " ",
          "finished_operations": " ",
          "liquidity": "",
          "add_metric": " ",
          "metrics_for": " ",
          "no_data_for_this_date": "    .",
          "created_at": " ",
          "website_visits_header": " ",
          "app_downloads_header": " ",
          "finished_operations_header": " ",
          "liquidity_header": "",
          "actions": "",
          "edit": "",
          "delete": "",
          "save": "",
          "cancel": "",
          "confirm_delete": "       "
        },
      },
    },
  });

export default i18n;
